<template>
  <div class="page">
    <DaoRule />
    <div class="info-box">
      <!-- logo -->
      <div class="key-value">
        <div class="key">
          {{ createDao.logo }} <span class="fu-title important">(100*100)</span>
        </div>
        <VanUploader
          class="logo-box"
          v-model="imageList"
          multiple
          accept="image/jpg, image/jpeg, image/png"
          :max-count="1"
          :after-read="handleUpLogo"
        >
          <div class="upload-icon">+</div>
        </VanUploader>
        <VanField
          v-if="false"
          class="input-box"
          input-align="center"
          v-model="params.logo"
          :placeholder="createDao.pleaseInput"
          maxlength="200"
        ></VanField>
      </div>
      <!-- 名字 中-->
      <div class="key-value">
        <div class="key">
          {{ createDao.daoName }}
          <span class="fu-title"> ({{ createDao.cn }})</span>
        </div>
        <VanField
          class="input-box"
          input-align="center"
          v-model="params.nameCn"
          :placeholder="createDao.pleaseInput"
          maxlength="40"
        ></VanField>
      </div>
      <!-- 名字 英-->
      <div class="key-value">
        <div class="key">
          {{ createDao.daoName }}
          <span class="fu-title"> ({{ createDao.en }})</span>
        </div>
        <VanField
          class="input-box"
          input-align="center"
          v-model="params.nameEn"
          :placeholder="createDao.pleaseInput"
          maxlength="40"
        ></VanField>
      </div>
      <!-- 简介 中-->
      <div class="key-value">
        <div class="key">
          {{ createDao.desc }}
          <span class="fu-title"> ({{ createDao.cn }})</span>
        </div>
        <VanField
          type="textarea"
          cols="10"
          rows="5"
          maxlength="100"
          class="textarea-box"
          v-model="params.descCn"
          show-word-limit
          :placeholder="createDao.pleaseInput"
        ></VanField>
      </div>
      <!-- 简介 英-->
      <div class="key-value">
        <div class="key">
          {{ createDao.desc }}
          <span class="fu-title"> ({{ createDao.en }})</span>
        </div>
        <VanField
          type="textarea"
          cols="10"
          rows="5"
          maxlength="200"
          class="textarea-box"
          v-model="params.descEn"
          show-word-limit
          :placeholder="createDao.pleaseInput"
        ></VanField>
      </div>
      <!-- 网站（选填）-->
      <div class="key-value">
        <div class="key">
          {{ createDao.webSite }}
          <span class="fu-title"> ({{ common.optional }})</span>
        </div>
        <VanField
          class="input-box"
          input-align="center"
          v-model="params.webSite"
          :placeholder="createDao.pleaseInput"
          maxlength="200"
        ></VanField>
      </div>
      <!-- Twitter（选填）-->
      <div class="key-value">
        <div class="key">
          {{ createDao.Twitter }}
          <span class="fu-title"> ({{ common.optional }})</span>
        </div>
        <VanField
          class="input-box"
          input-align="center"
          v-model="params.Twitter"
          :placeholder="createDao.pleaseInput"
          maxlength="200"
        ></VanField>
      </div>
      <!-- Telegram（选填）-->
      <div class="key-value">
        <div class="key">
          {{ createDao.Telegram }}
          <span class="fu-title"> ({{ common.optional }})</span>
        </div>
        <VanField
          class="input-box"
          input-align="center"
          v-model="params.Telegram"
          :placeholder="createDao.pleaseInput"
          maxlength="200"
        ></VanField>
      </div>
      <!--Discord（选填）-->
      <div class="key-value">
        <div class="key">
          {{ createDao.Discord }}
          <span class="fu-title"> ({{ common.optional }})</span>
        </div>
        <VanField
          class="input-box"
          input-align="center"
          v-model="params.Discord"
          :placeholder="createDao.pleaseInput"
          maxlength="200"
        ></VanField>
      </div>
      <!-- 代币合约地址-->
      <div class="key-value">
        <div class="key">
          {{ createDao.tokenAddress }}
          <span class="fu-title">(BEP20)</span>
        </div>
        <VanField
          class="input-box input-address"
          input-align="center"
          v-model="tokenAddress"
          :placeholder="createDao.pleaseInput"
        ></VanField>
        <div class="tips" v-if="isExit">{{ createDao.daoExit }}</div>
      </div>
    </div>
    <div class="token-info">
      <div>
        {{ createDao.tokenName }}:
        <VanLoading size="14" color="#00D093" v-if="loadingTokenName" />
        <span class="token-green" v-else>{{ tokenName }}</span>
      </div>
      <div>
        {{ createDao.totalSupply }}:
        <VanLoading size="14" color="#00D093" v-if="loadingSupply" />
        <span class="token-green" v-else>{{
          $gbUtils.formatNumber(totalSupply)
        }}</span>
      </div>
    </div>
    <div class="lp-info">
      <div class="key">{{ createDao.isSupportedLp }}</div>
      <div class="btn-groups">
        <VanButton
          class="btn"
          :class="{ active: isSupportLp }"
          @click="handleChange(true)"
        >
          {{ createDao.supported }}
        </VanButton>
        <VanButton
          class="btn"
          :class="{ active: !isSupportLp }"
          @click="handleChange(false)"
        >
          {{ createDao.notSupported }}
        </VanButton>
      </div>
      <!-- 请输入lp合约地址 -->
      <div class="key-valye" v-if="isSupportLp">
        <div class="key">
          {{ createDao.pleaseInput + createDao.lpAddress }}
          <span class="fu-title">(BEP20)</span>
        </div>
        <VanField
          class="input-box lp-address"
          input-align="center"
          v-model="lpAddress"
          :placeholder="createDao.pleaseInput"
          maxlength="200"
        ></VanField>
      </div>
    </div>
    <div class="token-info">
      <div>
        {{ createDao.needNum }}:
        <VanLoading size="14" color="#00D093" v-if="loadingSupply" />
        <span class="token-green" v-else>{{
          $gbUtils.formatNumber(totalSupply * point)
        }}</span>
      </div>
      <div>
        {{ createDao.balance }}:
        <VanLoading size="14" color="#00D093" v-if="loadingBalance" />
        <span class="token-green" v-else>{{
          $gbUtils.formatNumber(tokenBalance)
        }}</span>
      </div>
    </div>
    <div class="_flex">
       <!-- 授权按钮 -->
    <VanButton
      class="create-btn"
      :loading="loadingApprove"
      :disabled="!isTokenAddress || !tokenAddress || isExit || tokenAuth"
      @click="approveToken"
    >
      {{ common.Apporve }}
    </VanButton>
    <!-- 创建按钮 -->
    <VanButton
      class="create-btn"
      :loading="loadingAdd"
      :disabled="
        !tokenAuth ||
        !params.logo ||
        !params.nameCn ||
        !params.nameEn ||
        !params.descCn ||
        !params.descEn ||
        !tokenAddress ||
        !isTokenAddress ||
        isExit
      "
      @click="handleAdd"
    >
      {{ createDao.createDao }}
    </VanButton>
    </div>
  </div>
</template>

<script>
import daoObj from '../../contracts/contractDao'
import tokenObj from '../../contracts/contractToken'
import DaoRule from '../../components/DaoRule.vue'
import config from '../../config/app.config'
import { mapState } from 'vuex'
import { uploadFile } from '../../utils/upload'

export default {
  name: '',
  components: { DaoRule },
  data () {
    return {
      point: 0.01,
      imageList: [],
      params: {
        logo: '',
        nameCn: '',
        nameEn: '',
        descCn: '',
        descEn: '',
        Twitter: '',
        Telegram: '',
        Discord: '',
        webSite: ''
      },
      tokenAddress: '',
      isSupportLp: true,
      lpAddress: '',
      tokenName: '-',
      loadingTokenName: false,
      totalSupply: 0,
      loadingSupply: false,
      tokenAuth: false,
      tokenBalance: 0,
      loadingBalance: false,
      loadingApprove: false,
      loadingAdd: false,
      isExit: false, // 已存在
      isTokenAddress: false
    }
  },
  computed: {
    ...mapState(['myAccount']),
    rule () {
      return this.$t('createDao.rule', { point: this.point * 100 })
    },
    createDao () {
      return this.$t('createDao')
    },
    common () {
      return this.$t('common')
    }
  },
  watch: {
    tokenAddress (val) {
      this.getTokenInfo(val)
    }
  },
  methods: {
    onOversize (file) {
      this.$toast(this.createDao.logoSize)
    },
    async handleUpLogo (obj) {
      const resp = await uploadFile(obj.file)
      if (!resp.success) {
        this.uploadFile = []
        return
      }
      this.params.logo = resp.result
    },
    handleChange (falg) {
      this.isSupportLp = falg
    },
    async getTokenInfo (tokenAddress) {
      if (tokenAddress) {
        tokenAddress = tokenAddress.replace(/\s/g, '')
        this.isTokenAddress = this.$web3.isAddress(tokenAddress)
        if (!this.isTokenAddress) return
        this.getDaoExit()
        this.getTokenName()
        this.getUserBalance()
        await this.getTokenSupply()
        this.getTokenAllance()
      } else {
        this.tokenName = '-'
        this.totalSupply = 0
        this.tokenBalance = 0
        this.tokenAuth = false
        this.isExit = false
      }
    },
    async getDaoExit () {
      const resp = await daoObj.daoExist(this.tokenAddress)
      if (!resp.success) return
      this.isExit = resp.result
    },
    async getTokenName () {
      this.loadingTokenName = true
      const resp = await tokenObj.getSymbol(this.tokenAddress)
      this.loadingTokenName = false
      if (!resp.success) return
      this.tokenName = resp.result
    },
    async getTokenSupply () {
      this.loadingSupply = true
      const resp = await tokenObj.totalSupply(this.tokenAddress)
      this.loadingSupply = false
      if (!resp.success) return
      this.totalSupply = resp.result
    },
    async getTokenAllance () {
      const resp = await tokenObj.allowance(this.tokenAddress, config.daoAddress)
      if (!resp.success) return
      const allowanceAmount = resp.result
      this.tokenAuth = allowanceAmount >= (this.totalSupply * this.point)
    },
    async getUserBalance () {
      this.loadingBalance = true
      const resp = await this.$web3.balanceOf(this.tokenAddress)
      console.log(resp)
      this.loadingBalance = false
      if (!resp.success) return
      this.tokenBalance = this.$gbUtils.formatBalance(resp.result)
    },
    async approveToken () {
      this.loadingApprove = true
      const resp = await tokenObj.approve(this.tokenAddress, config.daoAddress)
      this.loadingApprove = false
      if (!resp.success) return
      this.tokenAuth = true
    },
    handleAdd () {
      if (this.isSupportLp && !this.lpAddress) { return this.$toast(this.createDao.pleaseInput + 'lpAddress') }
      if (this.isSupportLp && !this.$web3.isAddress(this.lpAddress)) return this.$toast('error lpAddress')
      if (parseFloat(this.tokenBalance) < this.totalSupply * this.point) {
        return this.$toast(this.common.notBalance)
      }
      const params = Object.values(this.params)
      const tokenAddress = this.tokenAddress
      const lpAddress = this.isSupportLp ? this.lpAddress : '0x0000000000000000000000000000000000000000'
      this.addDao(params, tokenAddress, lpAddress)
    },
    async addDao (params, tokenAddress, lpAddress) {
      await this.$web3.connectWallet()
      this.loadingAdd = true
      const resp = await daoObj.createDao(params, tokenAddress, lpAddress)
      this.loadingAdd = false
      if (!resp.success) return this.$toast('fail')
      this.$router.replace('/dao')
    }
  },
  mounted () { }
}
</script>

<style scoped lang="scss">
.page {
  padding: 20px 15px;
  font-size: $font-14;
  .info-box {
    .key-value {
      margin-top: 30px;
    }
    .logo-box {
      width: 94px;
      height: 94px;
      background: $base-color;
      box-shadow: $shadow;
      border-radius: 8px;
      display: inherit;
      margin: auto;
      // margin-bottom: 20px;
      .upload-icon {
        font-size: 38px;
        font-weight: bold;
        width: 94px;
        height: 94px;
        text-align: center;
        display: flex;
        justify-content: center;
        align-items: center;
        color: $color-gray2;
      }
    }
    .textarea-box {
      resize: none;
      padding-left: 10px;
      padding-top: 10px;
      width: 100%;
      border-radius: 8px;
      ::-webkit-input-placeholder {
        color: $color-gray2;
        font-size: $font-12;
      }
    }
    .input-box {
      &.input-address {
        height: 22px !important;
        line-height: 22px !important;
        background: $color-green !important;
        font-size: $font-12;
      }
    }
    .token-tips {
      color: $color-orange;
      font-size: $font-12;
    }
    /deep/ .van-field__value {
      width: 100%;
    }
  }
  .key {
    font-size: $font-14;
    color: $main-color1;
    margin-bottom: 10px;
  }
  .fu-title {
    font-size: $font-12;
  }
  .token-info {
    margin-top: 30px;
    color: $main-color1;
    line-height: 1.5em;
    font-weight: bold;
    .token-green {
      color: $color-green;
      margin-left: 5px;
    }
  }
  .lp-info {
    margin-top: 30px;
    .btn-groups {
      display: flex;
      align-items: center;
      justify-content: space-evenly;
      font-weight: bold;
      margin: 0 0 30px 0;
      .btn {
        width: 110px;
        height: 39px;
        background: $color-yellow;
        border-radius: 8px;
        color: $base-color;
        &.active {
          background: $color-green;
        }
      }
    }
    .lp-address {
      background: rgba(255, 255, 255, 0.2) !important;
      /deep/ input {
        color: $base-color !important;
      }
    }
  }

  .create-btn {
    width: 167px;
    height: 39px;
    background: $btn-blue;
    border-radius: 20px;
    color: $base-color;
    font-size: $font-16;
    font-weight: bold;
    margin: 35px auto;
  }
  /deep/.van-uploader__preview-image,
  .van-uploader__preview {
    width: 94px;
    height: 94px;
  }
  /deep/ .van-image__img {
    border-radius: 8px;
    object-fit: cover;
  }
  /deep/ .van-loading {
    display: inline;
  }
}
</style>
<style lang="scss">
.important {
  color: $color-orange;
  font-size: $font-12;
}
</style>
